import {
  isValidCnpj,
  isValidCpf,
  isValidCep,
  isValidPhone,
} from "@brazilian-utils/validators";
import validator from "validator";

export function isValid(id, value, type) {
  value = value === undefined ? "" : value;
  if (type === "cnpj") {
    if (isValidCnpj(value)) {
      return true;
    } else {
      return "CNPJ Inválido";
    }
  } else if (type === "Boolean") {
    return true;
  } else if (type === "Password") {
    if (value.length === 0) {
      return "O Campo não pode ficar vazio";
    } else {
      return true;
    }
  } else if (type === "UF") {
    if (value.length === 0) {
      return "O Campo não pode ficar vazio";
    } else {
      return true;
    }
  } else if (type === "StringNome") {
    if (value.length === 0) {
      return "O Campo não pode ficar vazio";
    } else if (value.split(" ").length < 2) {
      return "Insira seu nome completo";
    } else {
      return true;
    }
  } else if (type === "String") {
    if (value.trim().length === 0) {
      return "O Campo não pode ficar vazio";
    } else {
      return true;
    }
  } else if (type.toLowerCase() === "telephone") {
    if (value.trim().length === 0) {
      return "O Campo não pode ficar vazio";
    } else if (isValidPhone(value.trim())) {
      return true;
    } else {
      return "Telefone Inválido"
    }
  } else if (type === "Email") {
    if (value.trim().length === 0) {
      return "O campo não pode ficar vazio";
    } else if (validator.isEmail(value.trim())) {
      return true;
    } else {
      return "Email Inválido"
    }
  } else if (type === "cpf") {
    if (value.trim().length === 0) {
      return "O campo não pode ficar vazio"
    } else if (isValidCpf(value)) {
      return true;
    } else {
      return "CPF Inválido";
    }
  } else if (type === "rg") {
    if (value.trim().length === 0) {
      return "O campo não pode ficar vazio"
    } else {
      return true;
    }
  } else if (type === "cep") {
    if (isValidCep(value)) {
      return true;
    } else {
      return "CEP Inválido";
    }
  } else if (type.toLowerCase() === "number") {
    if (value.trim().length === 0) {
      return "Insira um numero";
    } else if (isNaN(value)) {
      return "Insira um numero valido";
    } else if (!isNaN(value) && (parseFloat(value) < 0)) {
      return "Numero precisa ser positivo";
    } else {
      return true;
    }
  } else if (type.toLowerCase() === "integer") {
    if (value.trim().length === 0) {
      return "Insira um numero";
    } else if (isNaN(value)) {
      return "Insira um numero valido";
    } else if (value < 0) {
      return "Numero precisa ser positivo";
    } else {
      return true;
    }
  } else if (type === "NotReqString") {
    return true;
  }
}
