import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import { Heading, HeadingLevel } from "baseui/heading";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import {
  createMuiTheme,
  makeStyles,
  MuiThemeProvider,
} from "@material-ui/core";
import Divider from "@material-ui/core/Divider";
import Input from "@material-ui/core/TextField";
import { isValid } from "./Validations";
import ReactInputMask from "react-input-mask";
import { FormControl } from "baseui/form-control";
import { Button } from "baseui/button";
import { Paragraph3 } from "baseui/typography";
import axios from "axios";
import { listaUF } from "./Constants";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  ModalButton,
  SIZE,
  ROLE,
} from "baseui/modal";
import { Radio } from "antd";
import imagem from "./images/imagem_cadastro.png";

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100vh",
  },
  image: {
    backgroundImage: "url(" + imagem + ")",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "top",
    backgroundColor: "#8de8f0",
    backgroundSize: "cover",
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));
var data = {
  address: {},
};
export default function Cadastro() {
  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: "light",
          primary: {
            light: "#94e8f0",
            main: "#02b4e7",
            dark: "#009fe7",
            contrastText: "#fff",
          },
          secondary: {
            light: "#ffddc1",
            main: "#ffab91",
            dark: "#c97b63",
            contrastText: "#000",
          },
        },
      }),
    []
  );
  const classes = useStyles();

  function validateField(id, value, type) {
    let valid = isValid(id, value, type);
    if (valid === true) {
      setErrors({
        ...errors,
        [id]: {
          error: false,
          message: "",
        },
      });
    } else {
      console.log("error id", id);
      setErrors({
        ...errors,
        [id]: {
          error: true,
          message: valid,
        },
      });
    }
    console.log("error", errors);
  }

  const [name, setName] = React.useState("");
  const [cpf, setCPF] = React.useState("");
  const [errors, setErrors] = React.useState({});
  const [dataNascimento, setDataNascimento] = React.useState("");
  const [cep, setCep] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [sex, setSex] = React.useState("male");
  const [celular, setCelular] = React.useState("");
  const [addressNumber, setAddressNumber] = React.useState("");
  const [address, setAddress] = React.useState("");
  const [addressCity, setAddressCity] = React.useState("");
  const [addressUF, setAddressUF] = React.useState([]);
  const [addressComplement, setAddressComplement] = React.useState("");
  const [addressNeighborhood, setAddressNeighborhood] = React.useState("");
  const [compraOnline, setCompraOnline] = React.useState("sim");
  const [frequencia, setFrequencia] = React.useState("");
  const [nextButtonDisabled, setNextButtonDisabled] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(false);
  const [isOpen, setIsOpen] = React.useState(false);
  const [message, setMessage] = React.useState("");

  function enableNextButton() {
    console.log("enable");
    setNextButtonDisabled(false);
  }

  function disableNextButton() {
    setNextButtonDisabled(true);
  }

  let fields = [
    { key: "name", value: name, type: "String" },
    { key: "cpf", value: cpf, type: "cpf" },
    { key: "birthDate", value: dataNascimento, type: "String" },
    { key: "cep", value: cep, type: "String" },
    { key: "email", value: email, type: "Email" },
    { key: "phone", value: celular, type: "telephone" },

    { key: "address", value: address, type: "String" },
    { key: "address_number", value: addressNumber, type: "String" },
    { key: "address_city", value: addressCity, type: "String" },
    { key: "address_uf", value: addressUF, type: "UF" },
    {
      key: "address_complement",
      value: addressComplement,
      type: "NotReqString",
    },
    { key: "address_neighborhood", value: addressNeighborhood, type: "String" },
    { key: "compra_online", value: compraOnline, type: "String" },
    { key: "frequencia", value: frequencia, type: "String" },
  ];

  React.useEffect(() => {
    function validateAll() {
      let i = 0;
      let res = false;
      while (i < fields.length) {
        let value = fields[i].value;
        if (typeof value === "object") {
          value = value[0].id;
        }
        let validated = isValid(fields[i].key, value, fields[i].type);
        if (validated !== true) {
          console.log("missing one", fields[i].key, value, fields[i].value);
          res = true;
          break;
        }
        i++;
      }
      if (res) {
        disableNextButton();
      } else {
        enableNextButton();
        console.log("habilitar botao");
      }
    }

    validateAll();
  }, [
    name,
    email,
    dataNascimento,
    cpf,
    cep,
    celular,
    addressCity,
    address,
    addressNumber,
    addressUF,
    addressComplement,
    addressNeighborhood,
    compraOnline,
    frequencia,
    fields,
  ]);

  function buscaViaCep(cep) {
    const MenuItems = {
      method: "GET",
      url: "https://viacep.com.br/ws/" + cep + "/json/",
    };

    axios
      .request(MenuItems)
      .then(function (response) {
        if (response.data.erro === true) {
          setErrors({
            ...errors,
            cep: {
              error: true,
              message: "CEP Nao encontrado",
            },
          });
        } else if (response.data) {
          data["address"]["street"] = response.data.logradouro;
          data["address"]["complement"] = response.data.complemento;
          data["address"]["uf"] = response.data.uf;
          data["address"]["city"] = response.data.localidade;
          data["address"]["neighborhood"] = response.data.bairro;
          setAddress(response.data.logradouro);
          setAddressComplement(response.data.complemento);
          // setAddressUF([
          //   listaUF.find((obj) => {
          //     return obj.id === response.data.uf;
          //   })]
          // );
          setAddressUF(response.data.uf);

          setAddressCity(response.data.localidade);
          setAddressNeighborhood(response.data.bairro);
        }
      })
      .catch(function (error) {
        console.error(error);
      });
  }

  function cadastrar() {
    data["sex"] = sex;
    setIsLoading(true);
    console.log("data", data);

    var config = {
      method: "post",
      url: "https://us-central1-brunofsociety-4204c.cloudfunctions.net/cadastroUsuario",
      headers: {
        "Content-Type": "application/json",
      },
      data: JSON.stringify(data),
    };

    axios(config)
      .then(function (response) {
        setIsLoading(false);
        console.log(JSON.stringify(response.data));
        if (response.data.status === 200) {
          setMessage(
            "Dados Enviados! Acesse seu email e obrigado pelo interesse!"
          );
        } else {
          setMessage(
            "Ops! Falha nossa, poderia nos enviar um email com o erro, linerappbr@gmail.com.\n\nerro=" +
              JSON.stringify(response.data)
          );
        }
        setIsOpen(true);
      })
      .catch(function (error) {
        setIsLoading(false);
        console.log(error);
        setMessage(
          "Ops! Falha nossa, poderia nos enviar um email com o erro, linerappbr@gmail.com.\n\nerro=" +
            JSON.stringify(error)
        );
        setIsOpen(true);
      });
  }

  return (
    <MuiThemeProvider theme={theme}>
      <Grid container component="main" className={classes.root}>
        <CssBaseline />
        <Modal
          onClose={() => setIsOpen(false)}
          closeable
          isOpen={isOpen}
          animate
          autoFocus
          size={SIZE.default}
          role={ROLE.dialog}
        >
          <ModalHeader>Aviso</ModalHeader>
          <ModalBody>{message}</ModalBody>
          <ModalFooter>
            <ModalButton
              onClick={() => {
                setIsOpen(false);
                window.location.href = "https://www.liner.app.br/";
              }}
            >
              OK
            </ModalButton>
          </ModalFooter>
        </Modal>
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid
          item
          xs={12}
          sm={12}
          md={5}
          component={Paper}
          elevation={6}
          square
          style={{ height: "1200px" }}
        >
          <div className={classes.paper}>
            <HeadingLevel>
              <Heading>Devolução é na Liner!</Heading>
            </HeadingLevel>
            {/* <HeadingLevel>
              <Paragraph3>
                Neste momento estamos realizando o pré cadastro de pessoas interessadas em receber pelo seu tempo
                na fila dos correios, preencha o formulario a seguir (é rapidinho!) e em breve entraremos em contato!
              </Paragraph3>
            </HeadingLevel> */}
            <form className={classes.form} noValidate>
              <Grid container spacing={1} direction={"row"}>
                <br />
                <Grid item lg={12} md={12} sm={12}>
                  <FormControl caption={() => errors["name"]?.message}>
                    <Input
                      placeholder="Nome Completo:"
                      clearable
                      type="StringNome"
                      id="name"
                      error={errors["name"]?.error}
                      positive={errors["name"]?.error === false}
                      onChange={(e) => {
                        setName(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["name"] = e.target.value;
                      }}
                      fullWidth
                    />
                  </FormControl>
                </Grid>
                {/*<Grid container spacing={2} direction={"row"}>*/}
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl caption={() => errors["email"]?.message}>
                    <Input
                      placeholder="Email:"
                      clearable
                      type="Email"
                      id="email"
                      error={errors["email"]?.error}
                      positive={errors["email"]?.error === false}
                      fullWidth
                      onChange={(e) => {
                        setEmail(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["email"] = e.target.value;
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item lg={6} md={12} sm={12}>
                  <ReactInputMask
                    mask="99/99/9999"
                    value={dataNascimento}
                    fullWidth
                    onChange={(e) => {
                      setDataNascimento(e.target.value);
                      validateField(
                        e.target.id,
                        e.target.value,
                        e.target.getAttribute("type")
                      );
                      data["birthDate"] = e.target.value;
                    }}
                    maskChar=" "
                    type="String"
                  >
                    {() => (
                      <div>
                        <FormControl
                          caption={() => errors["birthDate"]?.message}
                        >
                          <Input
                            placeholder="Data de Nascimento:"
                            clearable
                            type="String"
                            id="birthDate"
                            error={errors["birthDate"]?.error}
                            positive={errors["birthDate"]?.error === false}
                            fullWidth
                          />
                        </FormControl>
                      </div>
                    )}
                  </ReactInputMask>
                </Grid>
                {/*</Grid>*/}
                {/*<Grid container spacing={2}>*/}
                <Grid item lg={6} md={12} sm={12}>
                  <ReactInputMask
                    mask="999.999.999-99"
                    value={cpf}
                    onChange={(e) => {
                      setCPF(e.target.value);
                      validateField(e.target.id, e.target.value, "cpf");
                      data["cpf"] = e.target.value;
                    }}
                    maskChar=" "
                    fullWidth
                    type="Number"
                  >
                    {() => (
                      <div>
                        <FormControl caption={() => errors["cpf"]?.message}>
                          <Input
                            placeholder="CPF"
                            clearable
                            id="cpf"
                            error={errors["cpf"]?.error}
                            positive={errors["cpf"]?.error === false}
                            fullWidth
                          />
                        </FormControl>
                      </div>
                    )}
                  </ReactInputMask>
                </Grid>
                <Grid
                  item
                  lg={6}
                  md={12}
                  sm={12}
                  style={{ alignSelf: "center" }}
                >
                  <Radio.Group
                    style={{ width: "100%" }}
                    size="large"
                    onChange={(e) => {
                      setSex(e.target.value);
                      data["sex"] = e.target.value;
                    }}
                    value={sex}
                  >
                    <Radio value={"male"}>Homem</Radio>
                    <Radio value={"female"}>Mulher</Radio>
                    <Radio value={"other"}>Outro</Radio>
                  </Radio.Group>
                </Grid>
                {/*</Grid>*/}
                <Grid item xs={12}>
                  <br />
                  <Divider light color="primary" />
                  <br />
                </Grid>
                {/*<Grid container spacing={2}>*/}
                <Grid item lg={6} md={12} sm={12}>
                  <ReactInputMask
                    mask="99999-999"
                    value={cep}
                    onChange={(e) => {
                      setCep(e.target.value);
                      validateField(e.target.id, e.target.value, "String");
                      data["address"]["cep"] = e.target.value;
                      if (e.target.value.trim().length === 9) {
                        buscaViaCep(e.target.value.trim().replace("-", ""));
                      }
                    }}
                    maskChar=" "
                    type="Number"
                    fullWidth
                  >
                    {() => (
                      <div>
                        <FormControl caption={() => errors["cep"]?.message}>
                          <Input
                            placeholder="CEP"
                            clearable
                            id="cep"
                            error={errors["cep"]?.error}
                            positive={errors["cep"]?.error === false}
                            fullWidth
                          />
                        </FormControl>
                      </div>
                    )}
                  </ReactInputMask>
                </Grid>
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl caption={() => errors["address"]?.message}>
                    <Input
                      placeholder="Endereco"
                      clearable
                      type="String"
                      id="address"
                      value={address}
                      error={errors["address"]?.error}
                      positive={errors["address"]?.error === false}
                      fullWidth
                      onChange={(e) => {
                        setAddress(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["address"]["street"] = e.target.value;
                      }}
                    />
                  </FormControl>
                </Grid>
                {/*</Grid>*/}
                {/*<Grid container spacing={2}>*/}
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl
                    caption={() => errors["address_neighborhood"]?.message}
                  >
                    <Input
                      placeholder="Bairro"
                      clearable
                      type="String"
                      value={addressNeighborhood}
                      id="address_neighborhood"
                      error={errors["address_neighborhood"]?.error}
                      positive={errors["address_neighborhood"]?.error === false}
                      fullWidth
                      onChange={(e) => {
                        setAddressNeighborhood(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["address_neighborhood"] = e.target.value;
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl
                    caption={() => errors["address_complement"]?.message}
                  >
                    <Input
                      placeholder="Complemento"
                      clearable
                      type="NotReqString"
                      value={addressComplement}
                      id="address_complement"
                      error={errors["address_complement"]?.error}
                      positive={errors["address_complement"]?.error === false}
                      fullWidth
                      onChange={(e) => {
                        setAddressComplement(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["address"]["complement"] = e.target.value;
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl
                    caption={() => errors["address_number"]?.message}
                  >
                    <Input
                      placeholder="Numero"
                      clearable
                      type="Number"
                      value={addressNumber}
                      id="address_number"
                      error={errors["address_number"]?.error}
                      positive={errors["address_number"]?.error === false}
                      fullWidth
                      onChange={(e) => {
                        setAddressNumber(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["address"]["number"] = e.target.value;
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl caption={() => errors["address_uf"]?.message}>
                    <Select
                      id="address_uf"
                      value={addressUF}
                      placeholder="UF"
                      fullWidth
                      onChange={(e) => {
                        setAddressUF(e.target.value);
                        validateField("address_uf", e.target.value, "UF");
                        data["address"]["uf"] = e.target.value;
                      }}
                    >
                      {listaUF.map((item) => {
                        return (
                          <MenuItem value={item.id}>{item.label}</MenuItem>
                        );
                      })}
                    </Select>
                  </FormControl>
                </Grid>
                {/*</Grid>*/}
                {/*<Grid container spacing={2}>*/}
                {/*  */}
                <Grid item lg={6} md={12} sm={12}>
                  <FormControl caption={() => errors["address_city"]?.message}>
                    <Input
                      placeholder="Cidade"
                      clearable
                      value={addressCity}
                      type="String"
                      id="address_city"
                      error={errors["address_city"]?.error}
                      positive={errors["address_city"]?.error === false}
                      fullWidth
                      onChange={(e) => {
                        setAddressCity(e.target.value);
                        validateField(
                          e.target.id,
                          e.target.value,
                          e.target.getAttribute("type")
                        );
                        data["address"]["city"] = e.target.value;
                      }}
                    />
                  </FormControl>
                </Grid>
                {/*</Grid>*/}
                <Grid item xs={12}>
                  <ReactInputMask
                    mask="(99) 99999-9999"
                    value={celular}
                    fullWidth
                    onChange={(e) => {
                      setCelular(e.target.value);
                      validateField(
                        e.target.id,
                        e.target.value,
                        e.target.getAttribute("type")
                      );
                      data["phone"] = e.target.value;
                    }}
                    maskChar=" "
                    type="Telephone"
                  >
                    {() => (
                      <div>
                        <FormControl caption={() => errors["phone"]?.message}>
                          <Input
                            placeholder="Telefone"
                            clearable
                            type="Telephone"
                            id="phone"
                            error={errors["phone"]?.error}
                            positive={errors["phone"]?.error === false}
                            fullWidth
                          />
                        </FormControl>
                      </div>
                    )}
                  </ReactInputMask>
                </Grid>
                <Grid
                  item
                  lg={12}
                  md={12}
                  sm={12}
                  style={{ alignSelf: "center" }}
                >
                  Faz compras online com frequência?
                  <Radio.Group
                    style={{ width: "100%", marginTop: "10px" }}
                    size="large"
                    onChange={(e) => {
                      setCompraOnline(e.target.value);
                      data["compra_online_com_frequencia"] =
                        e.target.value === "sim";
                    }}
                    value={compraOnline}
                  >
                    <Radio value={"sim"}>Sim</Radio>
                    <Radio value={"nao"}>Nao</Radio>
                  </Radio.Group>
                </Grid>
                <Grid
                  item
                  lg={12}
                  md={12}
                  sm={12}
                  style={{ marginTop: "20px" }}
                >
                  Quantas devoluções por mês?
                  <FormControl caption={() => errors["frequencia"]?.message}>
                    <Select
                      id="frequencia"
                      value={frequencia}
                      placeholder="Frequencia"
                      fullWidth
                      onChange={(e) => {
                        setFrequencia(e.target.value);
                        validateField("frequencia", e.target.value, "String");
                        data["frequencia_devolucoes"] = parseFloat(
                          e.target.value
                        );
                      }}
                    >
                      <MenuItem value={"5"}>1 a 5 devoluções</MenuItem>
                      <MenuItem value={"10"}>5 a 10 devoluções</MenuItem>
                      <MenuItem value={"15"}>+ de 10 devoluções</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                  <Button
                    onClick={cadastrar}
                    disabled={nextButtonDisabled}
                    type="button"
                    style={{ width: "100%" }}
                    isLoading={isLoading}
                  >
                    CADASTRAR
                  </Button>
                </Grid>
              </Grid>
            </form>
          </div>
        </Grid>
      </Grid>
    </MuiThemeProvider>
  );
}
